<template>
  <v-container>
    <v-row>
        <v-col cols="12">
          <h1 class="display-2 font-weight-bold mb-3">Men</h1>
        </v-col>
    </v-row>

    <v-row>
      <v-col cols="12" md="3" lg="2">
        <v-card>
          <v-card-text style="text-align: center">
            <div>Record</div>
            <div class="text-h4 text--primary pt-6">{{wins}} - {{losses}}</div>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Home Runs</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{insidehrs}}</td>
                  <td>Inside the Park</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{outsidehrs}}</td>
                  <td>Over the Fence</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{totalhrs}}</td>
                  <td>Total Home Runs</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Run Differential</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{runsfor}}</td>
                  <td>Runs For</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{runsagainst}}</td>
                  <td>Runs Against</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12" lg="8">
        <v-data-table
          :headers="headers"
          :items="softballgames"
          :sort-by="['date', 'time']"
          :sort-desc="[false, false]"
          multi-sort
          disable-pagination
          hide-default-footer
          :loading="loadingTable"
          loading-text="Leading...Please wait"
          class="elevation-1"
        ></v-data-table>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
      <v-btn color="button_color" class="white--text" to="/">
        <v-icon dark left>mdi-home</v-icon>Home
      </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  title() {
    return "Softball Dashboard - Spring 2023 Men's Schedule"
  },
  data() {
    return {
      headers: [
        /*{ text: "Order", value: "order", align: "center" },*/
        /*{ text: "Type", value: "type" },*/
        { text: "Game Date", value: "date"},
        { text: "Opponent", value: "opponent" },
        { text: "Game Time", value: "time", align: "center" },
        { text: "Field", value: "field", align: "center" },
        { text: "Result", value: "results", align: "center"},
        { text: "Our Score", value: "our_score", align: "right" },
        { text: "Their Score", value: "their_score", align: "right" },
      ],
      softballgames: [],
      wins: 0,
      losses: 0,
      runsfor: 0,
      runsagainst: 0,
      insidehrs: 0,
      outsidehrs: 0,
      totalhrs: 0,
      loadingTable: true
    };
  },
  mounted(){
      this.loadData();
  },
  methods: {
    async loadData() {
      
      await axios.get(`https://gsx2json.com/api?api_key=${process.env.VUE_APP_API_KEY}&columns=false&id=16klK3voGD-oCUJfuS6Q2E74kHD4gm-dNwueeUG5IGis&sheet=Current&type=Men`)
        .then(response => {
          this.softballgames = response.data.rows;

          for (let i = 0; i < this.softballgames.length; i++) 
          {
            if(this.softballgames[i].results == "W") this.wins++;
            if(this.softballgames[i].results == "L") this.losses++;
            if(this.softballgames[i].our_score != undefined) this.runsfor += this.softballgames[i].our_score;
            if(this.softballgames[i].their_score != undefined) this.runsagainst += this.softballgames[i].their_score;
            
            if(this.softballgames[i].inside_hrs != undefined) 
            {
              this.insidehrs += this.softballgames[i].inside_hrs;
              this.totalhrs += this.softballgames[i].inside_hrs;
            }

            if(this.softballgames[i].outside_hrs != undefined) 
            {
              this.outsidehrs += this.softballgames[i].outside_hrs;
              this.totalhrs += this.softballgames[i].outside_hrs;
            }

            this.loadingTable = false;
          }
        })
        .catch(error => {
          console.error(error);
        });
    },
  },
};
</script>